import React, { useEffect, useState, useRef, useCallback } from 'react';

import Layout from '../components/layout';
import SEO from '../components/seo';
import Icon from '../images/inline/Icon404.svg';
import { Link } from 'gatsby';
import { trackCustomEvent } from 'gatsby-plugin-google-analytics';
import qrcode from 'qrcode-generator';
import db from 'app/database';
import lzstring from 'lz-string';
import jsqr from 'jsqr';
// import jsqr from 'qrcode-reader';

const WebVideo = () => {
  const canvasRef = useRef();

  useEffect(() => {
    const video = document.createElement('video');
    if (!canvasRef) return;

    if (!navigator.mediaDevices) return;

    const canvas = canvasRef.current;
    const c = canvas.getContext('2d');

    const tick = () => {
      if (video.readyState === video.HAVE_ENOUGH_DATA) {
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        c.drawImage(video, 0, 0, canvas.width, canvas.height);
        let imageData = c.getImageData(0, 0, canvas.width, canvas.height);
        // const data = jsqr.decode(imageData);
        const code = jsqr(imageData.data, imageData.width, imageData.height, {
          inversionAttempts: 'dontInvert',
        });
        // console.log({ code });

        // console.log(data);

        if (code) {
          const l = code.location;
          const spreadXY = loc => [loc.x, loc.y];
          c.beginPath();
          c.moveTo(...spreadXY(l.topLeftCorner));
          c.lineTo(...spreadXY(l.topRightCorner));
          c.lineTo(...spreadXY(l.bottomRightCorner));
          c.lineTo(...spreadXY(l.bottomLeftCorner));
          c.lineTo(...spreadXY(l.topLeftCorner));
          c.strokeStyle = 'curentColor';
          c.stroke();
          let data = lzstring.decompressFromUTF16(code.data);
          console.log(data);
        }
      }
      requestAnimationFrame(tick);
    };
    navigator.mediaDevices
      .getUserMedia({ video: { facingMode: 'environment ' } })
      .then(stream => {
        video.srcObject = stream;
        video.setAttribute('playsinline', true);
        video.play();
        requestAnimationFrame(tick);
      });
  }, []);

  return <canvas ref={canvasRef} />;
};

const NotFoundPage = () => {
  const [QrTag, setQrTag] = useState();
  useEffect(() => {
    db.caught.toArray(caught => {
      const qr = qrcode(0, 'L');

      console.log({ caught });

      let data = JSON.stringify({
        c: caught.map(c => `${c.type[0]}${c.no}`).join(''),
      });

      console.log({ data });
      const compressed = lzstring.compressToBase64(data);
      qr.addData(compressed);
      qr.make();
      setQrTag(qr.createSvgTag({ scalable: true }));
    });
  }, []);

  return (
    <Layout>
      <SEO title='Data' />
      <section>
        <h2>Heres all your Blathadex Data</h2>
        <p style={{ textAlign: 'center' }}>HOW DID WE GET HERE!?!</p>
        <div
          style={{ width: '50%', margin: 'auto' }}
          dangerouslySetInnerHTML={{ __html: QrTag }}
        />
        <WebVideo />
        <Icon />
        <p />
        <Link to='/'>
          <button>Back to Safety!</button>
        </Link>
      </section>
    </Layout>
  );
};

export default NotFoundPage;
